.btn_wrap {
  width:100%;
  li {display:inline-block;}
  &:after {
    @include clearfix();
  }
  &.btn_wrap_div1 {
    li {
      margin-top:$con_mg;
      width:100%;
      &:first-child {margin-top:0;}
    }
    .btnstl,
    .btnstl2,
    .btnstl3,
    .btnstl4,
    .btnstl5,
    .btnstl6 {
      width:100%;
    }
  }
  &.btn_wrap_div2 {
    .btnstl,
    .btnstl2,
    .btnstl3,
    .btnstl4,
    .btnstl5,
    .btnstl6  {
      float:left;
      width: calc(50% - 2.5px);
      margin-left: 5px;
      &:nth-child(2n+1) {
        margin-left:0;
      }
      &:nth-child(n+3) {
        margin-top:1%;
      }
    }
  }
  &.btn_wrap_div3 {
    .btnstl,
    .btnstl2,
    .btnstl3,
    .btnstl4,
    .btnstl5,
    .btnstl6  {
      float:left;
      width: calc(33.33% - 3.4px);
      margin-left: 5px;
      &:nth-child(3n+1) {
        margin-left:0;
      }
      &:nth-child(n+4) {
        margin-top:1%;
      }
    }
    &.except {
      .btnstl:nth-child(1) {
        width: calc(40% - 3.4px);
      }
      .btnstl:nth-child(2) {
        width: calc(30% - 3.4px);
      }
      .btnstl:nth-child(3) {
        width: calc(30% - 3.4px);
      }
    }
  }
  &.btn_wrap_div4 {
    .btnstl,
    .btnstl2,
    .btnstl3,
    .btnstl4,
    .btnstl5,
    .btnstl6  {
      float:left;
      width: calc(25% - 3.75px);
      margin-left: 5px;
      &:nth-child(4n+1) {
        margin-left:0;
      }
      &:nth-child(n+5) {
        margin-top:1%;
      }
    }
  }
  &.btn_wrap_div_share {
    .btnstl,
    .btnstl2,
    .btnstl3,
    .btnstl4,
    .btnstl5,
    .btnstl6  {
      float:right;
      width: calc(100% - 50px);
    }
    .btn_share {
      float:left;
      width:45px;
    }
  }
}

