.btn_switch {
  &_box{text-align: right;}
  position: relative;
  $btnWidth: 20px;
  $switchWidth: 45px;
  $switchHeight: 25px;
  $padding: 1.5px;
  .switch{display: none;}
  .switch + label{
    display: inline-block;
    position: relative;
    width: $switchWidth;
    height: $switchHeight;
    background: $white;  
    border-radius: 12.5px;
    padding: $padding;
    cursor: pointer;  
    @include css3-prefix('transition',  "all 0.2s ease");
    border: 1px solid $border_clr4;
    &:before{
      content: '';
      width: $btnWidth;
      height: $btnWidth;
      position: absolute;
      right: $padding;
      top: $padding;
      border-radius: 100%;
      background: #ccc;
      @include css3-prefix('transition',  "all 0.2s ease");
      
    }
    &:active{
      &:before{transform: scale(0.95);}
    }
  }
  .switch:checked + label{
    background: $point_clr;
    border: 1px solid $point_clr;
    &:before{
      content: '';
      background: $white;
      right: ($switchWidth - $btnWidth - $padding*2);
    }        
  }
}

